/* Theme Name: david */
button, [type='button'], [type='reset'], [type='submit'] {
  appearance: none;
  background-color: #1565c0;
  border: 0;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system, BlinkMacSystemFont, "Avenir Next", "Avenir", "Segoe UI", "Lucida Grande", "Helvetica Neue", "Helvetica", "Fira Sans", "Roboto", "Noto", "Droid Sans", "Cantarell", "Oxygen", "Ubuntu", "Franklin Gothic Medium", "Century Gothic", "Liberation Sans", sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  font-weight: 600;
  line-height: 1;
  padding: 0.75em 1.5em;
  text-align: center;
  text-decoration: none;
  transition: background-color 150ms ease;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
}

button:disabled, [type='button']:disabled, [type='reset']:disabled, [type='submit']:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

button:disabled:hover, [type='button']:disabled:hover, [type='reset']:disabled:hover, [type='submit']:disabled:hover {
  background-color: #1565c0;
}

fieldset {
  background-color: transparent;
  border: 0;
  margin: 0;
  padding: 0;
}

legend {
  font-weight: 600;
  margin-bottom: 0.375em;
  padding: 0;
}

label {
  display: block;
  font-weight: 600;
  margin-bottom: 0.375em;
}

input,
select,
textarea {
  display: block;
  font-family: -apple-system, BlinkMacSystemFont, "Avenir Next", "Avenir", "Segoe UI", "Lucida Grande", "Helvetica Neue", "Helvetica", "Fira Sans", "Roboto", "Noto", "Droid Sans", "Cantarell", "Oxygen", "Ubuntu", "Franklin Gothic Medium", "Century Gothic", "Liberation Sans", sans-serif;
  font-size: 16px;
}

[type='color'], [type='date'], [type='datetime'], [type='datetime-local'], [type='email'], [type='month'], [type='number'], [type='password'], [type='search'], [type='tel'], [type='text'], [type='time'], [type='url'], [type='week'], input:not([type]), textarea {
  appearance: none;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 3px;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  margin-bottom: 0.75em;
  padding: 0.5em;
  transition: border-color 150ms ease;
  width: 100%;
}

[type='color']:hover, [type='date']:hover, [type='datetime']:hover, [type='datetime-local']:hover, [type='email']:hover, [type='month']:hover, [type='number']:hover, [type='password']:hover, [type='search']:hover, [type='tel']:hover, [type='text']:hover, [type='time']:hover, [type='url']:hover, [type='week']:hover, input:not([type]):hover, textarea:hover {
  border-color: #b1b1b1;
}

[type='color']:focus, [type='date']:focus, [type='datetime']:focus, [type='datetime-local']:focus, [type='email']:focus, [type='month']:focus, [type='number']:focus, [type='password']:focus, [type='search']:focus, [type='tel']:focus, [type='text']:focus, [type='time']:focus, [type='url']:focus, [type='week']:focus, input:not([type]):focus, textarea:focus {
  border-color: #1565c0;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.06), 0 0 5px rgba(21, 101, 192, 0.7);
  outline: none;
}

[type='color']:disabled, [type='date']:disabled, [type='datetime']:disabled, [type='datetime-local']:disabled, [type='email']:disabled, [type='month']:disabled, [type='number']:disabled, [type='password']:disabled, [type='search']:disabled, [type='tel']:disabled, [type='text']:disabled, [type='time']:disabled, [type='url']:disabled, [type='week']:disabled, input:not([type]):disabled, textarea:disabled {
  background-color: #f2f2f2;
  cursor: not-allowed;
}

[type='color']:disabled:hover, [type='date']:disabled:hover, [type='datetime']:disabled:hover, [type='datetime-local']:disabled:hover, [type='email']:disabled:hover, [type='month']:disabled:hover, [type='number']:disabled:hover, [type='password']:disabled:hover, [type='search']:disabled:hover, [type='tel']:disabled:hover, [type='text']:disabled:hover, [type='time']:disabled:hover, [type='url']:disabled:hover, [type='week']:disabled:hover, input:not([type]):disabled:hover, textarea:disabled:hover {
  border: 1px solid #ddd;
}

[type='color']::placeholder, [type='date']::placeholder, [type='datetime']::placeholder, [type='datetime-local']::placeholder, [type='email']::placeholder, [type='month']::placeholder, [type='number']::placeholder, [type='password']::placeholder, [type='search']::placeholder, [type='tel']::placeholder, [type='text']::placeholder, [type='time']::placeholder, [type='url']::placeholder, [type='week']::placeholder, input:not([type])::placeholder, textarea::placeholder {
  color: #858585;
}

textarea {
  resize: vertical;
}

[type="checkbox"],
[type="radio"] {
  display: inline;
  margin-right: 0.375em;
}

[type="file"] {
  margin-bottom: 0.75em;
  width: 100%;
}

select {
  margin-bottom: 0.75em;
  width: 100%;
}

[type="checkbox"]:focus,
[type="radio"]:focus,
[type="file"]:focus,
select:focus {
  outline: 3px solid rgba(21, 101, 192, 0.6);
  outline-offset: 2px;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
}

ul,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

dl {
  margin: 0;
}

dt {
  font-weight: 600;
  margin: 0;
}

dd {
  margin: 0;
}

figure {
  margin: 0;
}

img,
picture {
  margin: 0;
  max-width: 100%;
}

table {
  border-collapse: collapse;
  margin: 1.5em 0;
  table-layout: fixed;
  text-align: left;
  width: 100%;
}

thead {
  line-height: 1.2;
  vertical-align: bottom;
}

tbody {
  vertical-align: top;
}

tr {
  border-bottom: 1px solid #ddd;
}

th {
  font-weight: 600;
}

th,
td {
  padding: 0.75em 0.75em 0.75em 0;
}

html {
  color: #333;
  font-size: 62.5%;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1.8rem;
  line-height: 1.2;
  margin: 0 0 0.75em;
}

p {
  margin: 0 0 0.75em;
}

a {
  text-decoration-skip: ink;
  transition: color 150ms ease;
}

a:hover {
  color: #104c90;
}

a:focus {
  outline: none;
  border: 0;
}

select:focus {
  outline: none;
  border: 0;
}

hr {
  border-bottom: 1px solid #ddd;
  border-left: 0;
  border-right: 0;
  border-top: 0;
  margin: 1.5em 0;
}

html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

.contact {
  border: 4px solid #dc6428;
  border-radius: 20px;
}

.contact .form-control {
  margin: 0px;
}

.contact h3 {
  color: #777;
}

.contact .txt-center {
  text-align: center;
}

.contact .err {
  color: red;
}

.contact .mst {
  background-color: red;
  color: #fff;
  margin-left: 10px;
  padding: 2px 10px;
}

.contact .errmessage {
  background-color: #faf6ec;
  color: #ed3f55;
}

.contact .errmessage li {
  padding: 10px;
  margin-bottom: 5px !important;
}

.contact .contact__inner {
  padding: 0px 30px 30px 30px;
}

.contact .contact__inner ul {
  padding: 0px;
}

.contact .contact__inner ul li {
  list-style: none;
  margin-bottom: 15px;
}

.contact .contact__inner ul li:last-child {
  margin-bottom: 0px;
}

.contact .contact-h2 {
  font-size: 30px;
  text-align: center;
  padding: 10px 0px;
  margin: 0px 0px 30px;
  color: #fff;
  background-color: #dc6428;
  border-radius: 10px 10px 0px 0px;
}

.contact .contact-h3 {
  font-size: 24px;
  text-align: center;
}

.contact .contact-h4 {
  font-size: 18px;
  text-align: left;
}

.contact .contact-step {
  text-align: center;
  margin-bottom: 40px;
}

.contact .contact-step li {
  display: inline-block;
  border: 1px solid #dc6428;
  color: #dc6428;
  padding: 10px 40px;
  margin-right: 20px;
}

.contact .contact-step li:last-child {
  margin-right: 0px;
}

.contact .contact-step .active {
  border: 1px solid #dc6428;
  background-color: #dc6428;
  color: #fff;
}

.insert-infomation {
  margin-bottom: 30px;
}

.insert-infomation::after {
  clear: both;
  content: "";
  display: block;
}

.insert-infomation dt {
  width: 20%;
  float: left;
  border-bottom: 1px solid #777;
  text-align: center;
  padding: 10px 0px;
}

.insert-infomation dd {
  width: 80%;
  float: left;
  margin: 0px;
  border-bottom: 1px solid #aaa;
  padding: 10px 0px 10px 10px;
}

.insert-infomation--input {
  display: table;
  width: 100%;
  border-bottom: 1px solid #aaa;
}

.insert-infomation--input::after {
  clear: both;
  content: "";
  display: block;
}

.insert-infomation--input dt, .insert-infomation--input dd {
  display: block;
  border-bottom: 1px solid #aaa;
  padding: 10px 0px 20px;
}

.insert-infomation--input dt:last-of-type, .insert-infomation--input dd:last-of-type {
  border: none;
}

.insert-infomation--input dt {
  width: 20%;
  float: left;
  line-height: 2.45;
}

.insert-infomation--input dd {
  width: 80%;
  float: left;
  margin: 0px;
  margin-bottom: 10px;
}

header {
  width: 100%;
  height: 500px;
  background-color: #333;
  margin-bottom: 80px;
}

footer {
  width: 100%;
  height: 300px;
  background-color: #aaa;
  margin-top: 120px;
}

h2 {
  font-size: 30px;
  color: #fff;
}

.content-width {
  width: 1000px;
  margin: 0 auto;
}

@media screen and (max-width: 1029px) {
  .content-width {
    width: 100%;
  }
}

.none {
  display: none;
}

table {
  width: 100%;
}

.btn_container {
  overflow: hidden;
  margin-bottom: 30px;
}

#before_week_btn, #next_week_btn {
  display: inline-block;
  padding: 0.3em 3em;
  text-decoration: none;
  color: #67c5ff;
  border: solid 2px #67c5ff;
  background-color: #fff;
  border-radius: 3px;
  transition: .4s;
}

@media screen and (max-width: 767px) {
  #before_week_btn, #next_week_btn {
    font-size: 1.2rem;
  }
}

#before_week_btn {
  float: left;
}

#next_week_btn {
  float: right;
}

.calendar-table {
  border: 1px solid #ddd;
  padding: 20px;
}

.calendar-table tr {
  font-size: 0px;
}

.calendar-table tr td {
  border-right: 1px solid #aaa;
  font-size: 14px;
  text-align: center;
  vertical-align: middle;
  height: 60px;
  font-weight: bold;
  padding: 0px;
  background-color: #faf6ec;
}

.calendar-table tr td:last-child {
  border-right: 0px solid #aaa;
}

@media screen and (max-width: 767px) {
  .calendar-table tr td {
    font-size: 1.0rem;
    width: 0%;
    height: 45px;
  }
}

.calendar-table tr .block {
  border-left: 1px solid #ddd;
  background-color: #fff;
}

.calendar-table tr .click {
  color: #bb5555;
}

.calendar-table tr .click:hover {
  cursor: pointer;
  background-color: #bb5555;
  color: #fff;
}

.calendar-table tr .insert_format_table_month {
  text-align: center;
  border: none !important;
  border-bottom: 1px solid #ddd;
  font-size: 16px;
  font-weight: bold;
  padding: 15px 0px;
  background-color: #fff;
}

@media screen and (max-width: 767px) {
  .calendar-table tr .insert_format_table_month {
    font-size: 1.0rem;
  }
}

.calendar-table tr .saturday {
  background-color: #d9e3ec;
}

.calendar-table tr .saturday p {
  color: #5190d5;
}

.calendar-table tr .sunday {
  background-color: #eeddd9;
}

.calendar-table tr .sunday p {
  color: #bb5555;
}

.calendar-table tr .insert_format_table_day {
  padding: 0px;
  border-right: 1px solid #aaa;
}

.calendar-table tr .insert_format_table_day:last-child {
  border-right: 0px solid #aaa;
}

.calendar-table tr .insert_format_table_day p {
  margin: 0px;
  text-align: center;
}
